
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from "@/components/Collapse/Collapse";
import showMore from "@/components/ShowMore/showMore"; 
export default {
components: {
XHeader,
Collapse,
showMore
},
data() {
return {
N: "",
CBH: "",
info: {},
}
},
mounted() {
this.getParams();
},
methods: {

getParams() {
var routerParams = this.$route.params;
this.N = routerParams.N;
this.CBH = routerParams.CBH;
this.getHighRiskReg();
},

getHighRiskReg() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "gaoweiService",
"MSH.2": "dj",
"N": this.N,
"CBH": this.CBH,
'token':this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
console.log(res);
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
}
};



